<template>
  <div>
    <my-header></my-header>
    <accounts-details-form></accounts-details-form>
    <accounts-details-table></accounts-details-table>
    <my-paginator :page-config="accountPageConfig" :page-name="pageName"></my-paginator>
  </div>
</template>

<script>
    import AccountsDetailsForm from "../components/AccountsDetailsForm";
    import AccountsDetailsTable from "../components/AccountsDetailsTable";
    import MyPaginator from "../components/MyPaginator";
    import {mapState} from "vuex";
    import MyHeader from "../components/MyHeader";
    export default {
        name: "AccountsDataDetails",
      components: {MyHeader, MyPaginator, AccountsDetailsTable, AccountsDetailsForm},
      data(){
          return{
            pageName: 'account'
          }
      },
      computed:{
        ...mapState(["accountPageConfig"]),
      },
    }
</script>

<style scoped>

</style>



