<template>
  <div class="auditedTable-box">
    <table cellspacing="1" class="auditedTable table">
      <thead>
      <tr>
        <th>已审核单号</th>
        <th>个人审核结果</th>
        <th>审核结果提交时间</th>
        <th>最终判定结果</th>
        <th>举报单详情</th>
      </tr>
      </thead>
      <tbody>
        <!--<tr>
          <td>46023354</td><td>违规</td><td>2018-11-15   19：30</td><td>违规</td><td><a href="javascript:;" class="link">%A5&game_id=2365</a></td>
        </tr>-->
        <tr v-for="(records,index) in judge_record" :key="index">
          <td>{{records.case_id}}</td>
          <td>{{records.personal_guilty}}</td>
          <td>{{records.judge_time}}</td>
          <td>{{records.final_result}}</td>
          <td>
            <a href="javascript:;" class="link" @click="seeInfo(records.case_id)">{{records.process_status}}</a>
          </td>
        </tr>
      </tbody>
    </table>
    <my-dialog-info v-show="pop === 'info'"
                    @hideInfo="pop = 'none'"
                    @showVideo="showVideo()"
                    :case-extra-info="caseExtraInfo"
                    :case-id="case_id"
    ></my-dialog-info>
    <my-dialog-video v-show="pop === 'video'"
                     @hideVideo="pop = 'info'"
                     :video-url="videoUrl"
    ></my-dialog-video>
  </div>
</template>

<script>
    import {mapState} from 'vuex';
    import MyDialogInfo from "./MyDialogInfo";
    import MyDialogVideo from "./MyDialogVideo";
    import {get_case_extra_info,query_court_case_info} from "../apis/getData"
    export default {
      name: "AccountsDetailsTable",
      components: {MyDialogVideo, MyDialogInfo},
      data(){
        return{
          pop: 'none',
          caseExtraInfo:null,
          videoUrl: null,
          case_id: null,
        }
      },
      computed: {
        ...mapState(["judge_record","game_id"]),
      },
      methods:{
        showVideo(){
          const params = {
            game_id: this.game_id,//游戏id, 取0表示全量游戏
            //user_id: 0,//用户id写0，实际从cookie中获取
            src_type: 0,
            op_type: 1,//查询视频信息
            case_id: this.case_id,// 案例id
          };
          query_court_case_info(params).then( response => {
            if (response.ret === 0) {
              this.pop = 'video';
              this.videoUrl = response.data.video_url;
            }else if (response.ret === this.$code.LOGINTIMEOUT) {
              this.$loginOut();
            }else{
              this.$onlyTxtPopShow(this.$errTxt.detailErr);
            };
          });
        },
        seeInfo(case_id){
          this.case_id = case_id;
          this.pop = 'info';
          const params = {
            game_id: this.game_id,//游戏id, 取0表示全量游戏
            //user_id: 0,//用户id写0，实际从cookie中获取
            src_type: 1,//请求源，写1,表示来自客服系统
            op_type: 0,//操作类型，写0，保留
            case_id: case_id,// 案例id
          };
          get_case_extra_info(params).then( response => {
            if (response.ret === 0) {
              this.caseExtraInfo = response.data;
            }else if (response.ret === this.$code.LOGINTIMEOUT) {
              this.$loginOut();
            }else{
              this.$onlyTxtPopShow(this.$errTxt.detailErr);
            };
          });
        }
      }

    }
</script>

<style scoped>

</style>



