import Qs from "qs";
import {setStore} from "@/assets/js/storage";
import {setting_auth_file} from "@/apis/getData"

export default {
  methods:{
    /*修改玩家权限*/
    modify_user_auth(op_type, modify_users, comment, dst_level) {
      let tableParams = {
        game_id: this.game_id,//游戏id, 取0表示全量游戏
        src_type: 0,
        op_type: op_type,//1：授权，2：取消(可恢复）；3：彻底删除；4：恢复
        modify_users: modify_users,//用户列表, “名单”输入框中的数据。授予权限的时候，需要以:分割的qq与open_id组合,如果是取消权限只需给出uin列表
        dst_level: dst_level,//设置的目标等级，0：普通管理者权限，1:A类审核权限, 2:B类审核权限, 3: C类审核权限
        comment: comment,//权限变更时的评论语,没有则为空
      };
      let params = null;
      params = tableParams;
      params.modify_users = JSON.stringify(params.modify_users);
      params = Qs.stringify(params);//处理参数
      let config = {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      };
      setting_auth_file(params, config).then(response => {
        if (response.ret === 0) {
          this.$store.commit('changeDialog', 'none');
          this.$store.commit('isReloadData', true);
          if (Number(op_type) === this.$op_type.resume) {
            setStore('op_type', this.$op_type.delete);
            this.$store.commit('indexSelectedOpType', this.$op_type.delete);
          } else {
            setStore('op_type', this.$op_type.resume);
            this.$store.commit('indexSelectedOpType', this.$op_type.resume);
          }
        } else if (response.ret === this.$code.NOAUTHORITY.num) {
          this.$onlyTxtPopShow(this.$code.NOAUTHORITY.txt);
        } else if (response.ret === this.$code.INCORRECTFORMAT.num) {
          this.$onlyTxtPopShow(this.$code.INCORRECTFORMAT.txt);
        } else if (response.ret === this.$code.REPETEOPERATION.num ) {
          this.$onlyTxtPopShow(this.$code.REPETEOPERATION.txt);
        } else if (response.ret === this.$code.LOGINTIMEOUT) {
          this.$loginOut();
        } else {
          this.$onlyTxtPopShow(this.$errTxt.setAuthErr);
        }
      })
    },
  }
}



