<template>
  <div class="tc-info pop">
    <a class="tc-info-close spr" @click="$emit('hideInfo')"></a>
    <div class="tc-info-num">
      <i></i><p>单号：{{caseId}}</p>
    </div>
    <div class="tc-info-content">
      <a class="tc-info-video"
         @click="$emit('showVideo')"
      ><span class="tc-video-play spr"></span>
      </a>
    </div>
    <div class="tc-info-title">
      <i></i><p>对局参考信息</p>
    </div>
    <ul class="tc-info-list" v-if="caseExtraInfo">
      <li><p>该英雄熟练度：</p><span>{{caseExtraInfo.hero_proficiency}}</span></li>
      <li><p>经济占比：</p><span>{{caseExtraInfo.pvp_coin_rate}}</span></li>
      <li><p>该英雄排位使用次数：</p><span>{{caseExtraInfo.hero_rank_total_count}}</span></li>
      <li><p>参团率：</p><span>{{caseExtraInfo.team_fight_rate}}</span></li>
      <li><p>该英雄排位胜率：</p><span>{{caseExtraInfo.hero_rank_win_rate}}</span></li>
      <li><p>本局掉线/重连次数：</p><span>{{caseExtraInfo.disconnect_reconnect_count}}</span></li>
      <li><p>承受伤害：</p><span>{{caseExtraInfo.hero_be_hurt_rate}}</span></li>
      <li><p>网络情况：</p><span>{{caseExtraInfo.net_type}}</span></li>
      <li><p>本局对其举报人数：</p><span>{{caseExtraInfo.complaint_count}}</span></li>
      <li><p>结算时金币：</p><span>{{caseExtraInfo.settle_pvp_coin}}</span></li>
    </ul>
  </div>
</template>

<script>
    export default {
        name: "MyDialogInfo",
      props: ["caseExtraInfo","caseId"],
    }
</script>

<style scoped>

</style>



