<template>
  <div class="video-pop pop" id="video-pop">
    <a @click="$emit('hideVideo')"
       class="tc-info-close spr"></a>
    <div id="container">
      <video id="video" :src="videoUrl" controls="controls" autoplay></video>
    </div>
  </div>
</template>

<script>
    export default {
      name: "MyDialogVideo",
      props: ["videoUrl"],
    }
</script>

<style scoped>

</style>



