<template>
  <div>
  <div class="auditEnd">
    <div class="auditEnd-box">
      <div class="infor-box">
        <p class="start-time">开始时间</p>
        <p class="end-time">结束时间</p>
      </div>
      <DatePicker class="date-start"
                  type="date"
                  :placeholder="initStartTime"
                  v-model="formData.start_time"></DatePicker>
      <DatePicker class="date-end"
                  type="date"
                  :placeholder="initEndTime"
                  v-model="formData.end_time"></DatePicker>
      <a href="javascript:;"
         class="btn-refresh"
         @click="checkTime()"
      >刷新</a>
      <a class="btn-return" @click="goBack()">返回</a>
      <div class="audit-infor">
        <p class="audit-person">审核人员：<span></span></p>
        <p class="audit-level">审核等级：<span></span></p>
        <p class="auditer">宗师审判者：<span></span></p>
        <p class="audit-percent">审核准确率：
          <span>{{week_accuracy}} </span>
          &nbsp;&nbsp;(近一周)&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          <span>{{month_accuracy}} </span>
          &nbsp;&nbsp;(近一月)
        </p>
        <a href="javascript:;"
           class="btn-resetAudit"
           v-if="Number(candleOpType) === $op_type.delete"
           @click="$store.commit('changeDialog','deleteAuthority');"
        >取消审核权限</a>
        <a href="javascript:;"
           class="btn-resetAudit"
           v-else
           @click="$store.commit('changeDialog','deleteAuthority');"
        >恢复审核权限</a>
      </div>
    </div>
  </div>
    <my-dialog @deleteAuthority="deleteAuthority"></my-dialog>
  </div>
</template>

<script>
    import {mapState} from 'vuex';
    import {query_judge_record} from "../apis/getData";
    import modify_user_auth from "../mixins/modify_user_auth";
    import MyDialog from "./MyDialog";
    import {getStore} from "../assets/js/storage";
    export default {
      name: "AccountsDetailsForm",
      components: {MyDialog},
      mixins: [modify_user_auth],
      data(){
        return{
          pageData: {
            offset: 0,//查询偏移量
            limit: 10,//本次要查询的个数
          },
          formData:{
            start_time: null,//开始时间
            end_time: null,//结束时间
          },
          default_dst_level: 1, //设置权限dst_level默认值
          candleOpType: null,
        }
      },
      computed: mapState({
        accountCurrent: state => state.accountPageConfig.current,
        indexSelectedOpType: state => state.indexSelectedOpType,
        game_id: state => state.game_id,
        week_accuracy(){
          return getStore('week_accuracy');
        },
        month_accuracy(){
          return getStore('month_accuracy');
        },
        user_id(){
          return this.$route.query.userId || getStore('user_id');
        },
        initStartTime(){
          return getStore('start_time');
        },
        initEndTime(){
          return getStore('end_time');
        },
        deleteAuthorityId(){
          let arr = [];
          arr.push(this.user_id);
          return arr;
        },
      }),
      watch:{
        accountCurrent(newVal){
          this.pageData.offset = (newVal-1)*this.pageData.limit;
          this.checkTime();
        },
        indexSelectedOpType(newVal){
          this.candleOpType = newVal;
        },
        user_id(newVal){
          this.checkTime();
        }
      },
      mounted(){
        this.checkTime();
        this.candleOpType = getStore('op_type');
      },
      methods: {
        deleteAuthority(data){
          this.modify_user_auth(this.candleOpType,this.deleteAuthorityId,data,this.default_dst_level)
        },
        checkTime(){
          let start_time = this.formData.start_time===""?this.initStartTime:this.$timeFormat(this.formData.start_time);
          let end_time = this.formData.end_time===""?this.initEndTime:this.$timeFormat(this.formData.end_time);
          if(start_time > end_time){
            this.$onlyTxtPopShow(this.$errTxt.selectTimeErr);
            return;
          };// todo分页还有问题。因为分页点击数字页后当前页的值变了，但是表单条件不满足*/
          let formatTime = {
            start_time: this.formData.start_time? this.$timeFormat(this.formData.start_time) : this.initStartTime,
            end_time : this.formData.end_time? this.$timeFormat(new Date(this.formData.end_time).getTime()+1000*60*60*24-1) : this.initEndTime
          };
          const params = {
            game_id: this.game_id,//游戏id, 取0表示全量游戏
            src_type: 0,
            query_user_id: this.user_id,//查询的用户账号
            op_type: 0,//操作类型，写0，保留
            offset: this.pageData.offset,//查询偏移量
            limit: this.pageData.limit,//本次要查询的个数
            ...formatTime
          };
          query_judge_record(params).then(response => {
            if (response.ret === 0) {
              this.$store.commit("judge_record",response.data.record_list);
              this.$store.commit("accountPageTotal",response.data.total);
              if(response.data.record_list.length === 0){
                this.$onlyTxtPopShow(this.$errTxt.noData);
              }
            }else if (response.ret === this.$code.LOGINTIMEOUT) {
              this.$loginOut();
            }else if (response.ret === this.$code.TOOLONGTIME.num) {
              this.$onlyTxtPopShow(this.$code.TOOLONGTIME.txt);
            }else{
              this.$onlyTxtPopShow(this.$errTxt.getDataErr);
            };
          });
        },
        goBack(){
          this.$store.commit('indexSelectedOpType',null);
          this.$router.push("/");
        }
      }
    }
</script>

<style scoped>

</style>



